<template>
    <div class="present">
        <ul class="routes">
            <li>
                <router-link to="/">首页</router-link>
            </li>
            <li>
                <router-link to="/money">我的余额</router-link>
            </li>
            <li>
                <router-link to="/money/present">余额转赠</router-link>
            </li>
        </ul>
        <div class="step step-1" v-if="step===1">
            <ul class="ac">
                <li><span class="step-icon ac bold active">1</span></li>
                <li><img class="step-line" src="../../assets/img/money/line_1.png" alt=""></li>
                <li><span class="step-icon ac bold ">2</span></li>
                <li><img class="step-line" src="../../assets/img/money/line_2.png" alt=""></li>
                <li><span class="step-icon ac bold ">3</span></li>
            </ul>
            <div class="step-form">
                <h2 class="ac bold">余额转赠</h2>
                <form>
                    <table class="row clearfix">
                        <tr class="row col-12">
                            <td class="t-tile col-2">我的余额：</td>
                            <td class="t-content col-10"><span style="font-size: 18px;font-weight: bold;color: #ff0077">{{userinfo.point|change_unit}}</span>{{this.storage.getStorage('exchange').unit}}</td>
                        </tr>
                        <tr class="row col-12">
                            <td class="t-tile col-2">对方账号：</td>
                            <td class="t-content col-10"><input v-model="account" placeholder="请输入对方手机号码" type="text"></td>
                        </tr>
                        <tr class="row col-12">
                            <td class="t-tile col-2">转赠数量：</td>
                            <td class="t-content col-10"><input v-model="point" placeholder="请输入转赠的数量" type="text"></td>
                        </tr>
                        <tr class="row col-12">
                            <td class="t-tile col-2">转赠说明：</td>
                            <td class="t-content col-10"><textarea v-model="notes" placeholder="转赠说明"
                                                                   name="noresize"></textarea></td>
                        </tr>
                        <tr class="row col-12">
                            <td class="t-tile col-2">&nbsp;&nbsp;</td>
                            <td class="t-content col-10">
                                <div class="net-step bold ac cursor" :class="{disabled:!nextStep}" @click="CheckUserInfo">下一步</div>
                            </td>
                        </tr>
                    </table>
                </form>
            </div>
        </div>
        <div class="step step-2" v-if="step===2">
            <ul class="ac">
                <li><span class="step-icon ac bold active">1</span></li>
                <li><img class="step-line" src="../../assets/img/money/line_3.png" alt=""></li>
                <li><span class="step-icon ac bold active">2</span></li>
                <li><img class="step-line" src="../../assets/img/money/line_1.png" alt=""></li>
                <li><span class="step-icon ac bold ">3</span></li>
            </ul>
            <div class="step-form">
                <h2 class="ac bold">余额转赠</h2>
                <form>
                    <table class="row clearfix">
                        <tr class="row col-12">
                            <td class="t-tile col-2">对方账号：</td>
                            <td class="t-content col-10"><span>{{account}}</span></td>
                        </tr>
                        <tr class="row col-12">
                            <td class="t-tile col-2">会员名称：</td>
                            <td class="t-content col-10"><span>{{userinfo.username}}</span></td>
                        </tr>
                        <tr class="row col-12">
                            <td class="t-tile col-2">转赠数量：</td>
                            <td class="t-content col-10"><span>{{point}}</span></td>
                        </tr>
                        <tr class="row col-12">
                            <td class="t-tile col-2">转赠说明：</td>
                            <td class="t-content col-10"><span>{{notes}}</span></td>
                        </tr>
                        <tr class="row col-12">
                            <td class="t-tile col-2">身份校验：</td>
                            <td class="t-content col-10">
                                <input class="account-input" type="text" v-model="sms_code" placeholder="请输入手机验证码">
                                <a class="button ac">
                                    <Coder :phone="userinfo.mobile"></Coder>
                                </a>
                            </td>
                        </tr>
                        <tr class="row col-12">
                            <td class="t-tile col-2">&nbsp;&nbsp;</td>
                            <td class="t-content col-10">
                                <div class="net-step bold ac cursor" :class="{disabled:!nextStepNi}" @click="send">确&nbsp;&nbsp;&nbsp;&nbsp;定</div>
                            </td>
                        </tr>
                    </table>
                </form>
            </div>
        </div>
        <div class="step step-3" v-if="step===3">
            <img src="../../assets/img/money/icon_success.png" alt="">
            <p class="bold" style="color: black">恭喜您，您的余额转赠成功，福分已存入对方账户</p>
            <div class="clearfix ac">
                <div class="step-3-btn fl ac bold cursor">
                    <router-link to="/money">查看我的余额</router-link>
                </div>
                <div class="step-3-btn fl ac bold cursor">
                    <router-link to="/">返回个人中心</router-link>
                </div>
            </div>
        </div>
        <div class="step step-3" v-if="step===4">
            <img src="../../assets/img/money/icon_fail.png" alt="">
            <p class="bold">呀，服务器开小差了，您的余额转赠失败</p>
            <div class="clearfix ac">
                <div class="step-3-btn fl ac bold cursor">
                    <router-link to="/money">查看我的福分</router-link>
                </div>
                <div class="step-3-btn fl ac bold cursor">
                    <router-link to="/">返回个人中心</router-link>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    let display = {
        showRecommend: false,
        nav: 'hidden',
        header: 'fault'
    };
    import Coder from '../../../../components/VerifyCode'

    export default {
        name: "present",
        metaInfo: {
            title: '积分转赠-个人中心', // set a title
            meta: [{                 // set meta
                name: '福分通',
                content: '专注企业员工福利'
            }]
        },
        created() {
            this.$store.dispatch('layout/Change', display);
            if (this.storage.hasItem('FFT')) {
                this.getMemberInfo()
            }
        },
        data() {
            return {
                step: parseInt(this.$route.query.step)|| 1 ,
                account: '',
                point: '',
                notes: '',
                sms_code : '',
                userinfo: {}
            }
        },
        mounted(){
            window.onpopstate = ()=>{
                this.back()
            }
        },
        components: {
            Coder
        },
        // watch:{
        //     step:{
        //         handler(n,o){
        //            console.log(n,o)
        //         },deep:true
        //     }
        // },
        methods: {
            getMemberInfo() {
                this.yum('api/member/getMemberInfo').then(res => {
                    if (res.data.status === 200) {
                        this.userinfo = res.data.data
                    }
                })
            },
            CheckUserInfo() {
                if(!this.account){
                    this.bus.$emit('notice', '请填写对方手机号码')
                    return false;
                }
                 let testPhone = /^1([358][0-9]|4[579]|66|7[0135678]|9[89])[0-9]{8}$/;
                 if(!testPhone.test(this.account)){
                     this.bus.$emit('notice', '手机号码不符合规范')
                     return false
                 }
                // 积分四舍五入
                if (this.storage.getStorage('exchange').exchange == 10){
                    this.point = Math.round(this.point,1);
                } else{
                    this.point = Math.round(this.point);
                }
                if(!this.point || this.point <= 0 || this.point>this.userinfo.point){
                    this.bus.$emit('notice', '转赠数量必须大于0的正整数,且不超过剩余数量')
                    return false;
                }
                if(!this.notes){
                    this.bus.$emit('notice', '请填写转赠说明')
                    return false;
                }
                this.$router.push({
                    query: {
                        step: 2
                    }
                });
                this.step = 2
            },
            send() {
                if(!this.sms_code){
                    this.bus.$emit('notice', '验证码必填')
                    return false;
                }
                this.yum('api/member_point/transfer', {
                    mobile : this.account,
                    jifen : parseInt(this.point)*this.storage.getStorage('exchange').exchange,
                    note : this.notes,
                    sms_code : this.sms_code
                }).then(
                    res => {
                        if (res.data.status === 200) {
                            this.$router.push({
                                query: {
                                    step: 3
                                }
                            });
                            this.step = 3
                        } else {
                            this.bus.$emit('notice', res.data.msg)
                        }
                    }
                )
            },
            msg() {
                window.alert('发送验证码')
            },
            back(){
                if (this.step === 1 ){
                    return
                } else {
                    this.step -= 1
                }
            }
        },
        computed:{
            nextStep(){
                if (this.account&&this.point&&this.notes){
                    return true
                }else{
                    return false
                }
            },
            nextStepNi(){
                if(this.sms_code){
                    return true
                }else {
                    return false
                }
            }
        }
    }
</script>

<style scoped lang="stylus">
    .present
        min-width 100%
        margin 40px 0 0 0
        position relative
        &.router
            margin-left 0

    ul.routes
        position relative
        li
            position relative
            display inline-block
            &:after
                content '>'
                margin 0 10px
            &:last-child:after
                display none

    .step
        width 720px
        margin 55px auto 70px
        li
            display inline-block
        &-icon
            display inline-block
            width 35px
            height 35px
            line-height 35px
            font-size 20px
            color white
            background-color #dcdcdc
            margin 0 3px
            border-radius 50%
            &.active
                background-color #0cbc5a
        &-line
            display inline-block
            margin 0 3px
        &-form
            background-color #f2f2f2
            padding 20px 0
            margin-top 50px
            color black
            h2
                font-size 20px
                margin 0 10px 20px
                padding-bottom 20px
                border-bottom 1px solid #ccc
            form
                width 480px
                margin 0 auto
            table
                width 100%
                tr
                    min-height 20px
                    line-height 40px
                    margin-bottom 20px
                    position relative
                    .net-step
                        font-size 16px
                        color white
                        background-color #d60073
                        border-radius 4px
                        &:hover
                            background-color #ff0077
                        &.disabled
                            background-color #ccc
                    input, textarea
                        padding 5px
                        width 100%
                        outline none
                        border 1px solid #ccc
                        border-radius 4px
                        &:focus
                            border 1px solid #ff0000
                    input
                        height 40px
                    textarea
                        resize none
                        line-height 40px
                        height 80px
                    input.account-input
                        width 300px
                        border-radius 0
                        position absolute
                        bottom 0
                    a.button
                        background-color #dcdcdc
                        border 1px solid #ccc
                        position absolute
                        bottom 0
                        right 0
                        width 100px
                        height 40px
                        outline none

        &.step-3
            position relative
            img
                display block
                margin 80px auto 0
                width 80px
            p
                font-size 20px
                margin-top 50px
                text-align center
            .clearfix
                width 330px
                margin 50px auto 0
            .step-3-btn
                width 150px
                height 48px
                line-height 48px
                font-size 16px
                border-radius 4px
                background-color #e4e4e4
                a
                    display block
                    color inherit
                    font-weight inherit
                &:hover
                    background-color #ff0077
                    color white
                &:last-child
                    margin-left 30px
</style>