<template>
    <div class="verify-sms">
        <div class="coder" v-if="btn" @click="SendVerifyCode"> {{ msg }}</div>
        <div class="coder disabled" v-else> {{ msg }}</div>
    </div>
</template>

<script>
    export default {
        name: "VerifyCode",
        data() {
            return {
                msg: '获取验证码',
                btn: true
            }
        },
        props: {
            phone: {
                default: '',
                type: String
            }
        },
        methods: {
            SendVerifyCode() {
                let data = {
                    phone: this.phone
                };
                this.yum('api/verify/sendSms', data).then(res => {
                    if (res.data.status === 200) {
                        this.Countdown();
                        this.bus.$emit('notice',res.data.msg)
                    } else {
                        this.bus.$emit('notice',res.data.msg)
                    }
                })
            },
            Countdown() {
                let time = 60;
                this.btn = false;
                let timer = setInterval(() => {
                    time -= 1;
                    this.msg = `请等待${time}秒`;
                    if (time === 0) {
                        clearInterval(timer);
                        this.btn = true;
                        this.msg = '获取验证码'
                    }
                }, 1000)

            }
        }
    }
</script>

<style scoped>
    .verify-sms {
        display: block;
    }

    .verify-sms div {
        display: block;
        height: 100%;
    }

    .verify-code div.disabled {

    }
</style>